import { AIR_STATE, CELL_TYPE } from './const';
import EnemyFleet, { Stage2Table } from './enemy/enemyFleet';
import Landbase from './landbase/landbase';
import Fleet from './fleet/fleet';
import Item from './item/item';
import CommonCalc from './commonCalc';

export default class Calculator {
  /**
   * 
   * @static
   * @param {Landbase[]} landbases
   * @param {EnemyFleet} enemyFleet
   * @param {number} battle
   * @memberof Calculator
   */
  public static calculateLandbasePhase(landbases: Landbase[], enemyFleet: EnemyFleet, battle: number): void {
    const stage2List = enemyFleet.stage2;
    for (let i = 0; i < landbases.length; i += 1) {
      const landbase = landbases[i];
      const wave1 = landbase.battleTarget[0];
      const wave2 = landbase.battleTarget[1];
      const isSeparate = wave1 !== wave2;

      // 1 2
      if (wave1 !== battle && wave2 !== battle) {
        continue;
      }

      /** =======  ======= */
      if (landbase.hasJet) {
        Calculator.ShootDownLandbaseJet(landbase, stage2List);
      }

      /** =======  1 ======= */
      if (wave1 === battle) {
        const state = CommonCalc.getAirState(landbase.airPower, enemyFleet.landbaseAirPower);
        // 
        landbase.resultWave1.rates[state] += 1;
        landbase.resultWave1.loopSumAirPower += landbase.airPower;
        landbase.resultWave1.loopSumEnemyAirPower += enemyFleet.landbaseAirPower;

        // 
        Calculator.shootDownEnemy(state, enemyFleet);
        if (isSeparate) {
          // 
          Calculator.ShootDownLandbase(state, landbase, stage2List);
        }
      }

      /** =======  2 ======= */
      if (wave2 === battle) {
        if (isSeparate) {
          // 
          Calculator.ShootDownLandbaseJet(landbase, stage2List);
        }

        const state = CommonCalc.getAirState(landbase.airPower, enemyFleet.landbaseAirPower);
        // 
        landbase.resultWave2.rates[state] += 1;
        landbase.resultWave2.loopSumAirPower += landbase.airPower;
        landbase.resultWave2.loopSumEnemyAirPower += enemyFleet.landbaseAirPower;

        // 
        Calculator.shootDownEnemy(state, enemyFleet);
        if (isSeparate) {
          // 
          Calculator.ShootDownLandbase(state, landbase, stage2List);
        }
      }
    }
  }

  /**
   * 
   * @static
   * @param {Fleet} fleet
   * @param {EnemyFleet} enemyFleet
   * @param {number} battle
   * @param {boolean} [skipStage2=true] true
   * @memberof Calculator
   */
  public static calculateMainPhase(fleet: Fleet, enemyFleet: EnemyFleet, battle: number, calcStage2 = false): void {
    const stage2List = enemyFleet.stage2;
    const result = fleet.results[battle];
    // todo 
    const state = CommonCalc.getAirState(fleet.airPower, enemyFleet.airPower, fleet.hasPlane || enemyFleet.hasPlane);

    // 
    result.addRates(state);
    result.loopSumAirPower += fleet.airPower;
    result.loopSumEnemyAirPower += enemyFleet.airPower;

    // 
    if (calcStage2) {
      // 
      Calculator.shootDownEnemy(state, enemyFleet, fleet.stage2);
    }
    // 
    const disabledStage2 = enemyFleet.cellType === CELL_TYPE.AIR_RAID || enemyFleet.cellType === CELL_TYPE.NIGHT;
    Calculator.shootDownFleet(state, fleet, stage2List, battle, disabledStage2);
  }

  /**
   *  2
   * 
   * @static
   * @param {Fleet} fleet
   * @param {EnemyFleet} enemyFleet
   * @memberof Calculator
   */
  public static calculateAerialConbatCellPhase(fleet: Fleet, enemyFleet: EnemyFleet): void {
    // todo 
    const state = CommonCalc.getAirState(fleet.airPower, enemyFleet.airPower, fleet.hasPlane);
    // 
    Calculator.shootDownFleet(state, fleet, enemyFleet.stage2);
  }

  /**
   *  
   * Fleet: airPower
   * Item: airPower, slot
   * @static
   * @param {Landbase} landbase
   * @param {Stage2Table[]} stage2List
   * @memberof Calculator
   */
  public static shootDownFleetJet(fleet: Fleet, enemyFleet: EnemyFleet): void {
    if (fleet.hasJet
      && (enemyFleet.cellType === CELL_TYPE.NORMAL || enemyFleet.cellType === CELL_TYPE.GRAND)
      && !enemyFleet.isAllSubmarine) {
      //  /  
      const sumAirPower = Calculator.shootDownJetPhase(fleet.allPlanes, enemyFleet.stage2);
      // 
      fleet.airPower = Math.floor(sumAirPower);
    }
  }

  /**
   *  
   * Landbase: airPower
   * Item: airPower, slot
   * @private
   * @static
   * @param {Landbase} landbase
   * @param {Stage2Table[]} stage2List
   * @memberof Calculator
   */
  private static ShootDownLandbaseJet(landbase: Landbase, stage2List: Stage2Table[]) {
    const sumAirPower = Calculator.shootDownJetPhase(landbase.items, stage2List);
    // 
    landbase.airPower = Math.floor(sumAirPower * landbase.reconCorr);
  }

  /**
   *  
   * Landbase: airPower
   * Item: airPower, slot
   * @private
   * @static
   * @param {Landbase} landbase
   * @param {Stage2Table[]} stage2List
   * @memberof Calculator
   */
  private static ShootDownLandbase(state: number, landbase: Landbase, stage2List: Stage2Table[]) {
    let sumAirPower = 0;
    const randomRange = stage2List[0].fixDownList.length;
    const { items } = landbase;
    for (let j = 0; j < items.length; j += 1) {
      const item = items[j];
      // ====== STAGE1 ======
      item.slot -= Math.floor(0.6 * CommonCalc.getStage1ShootDownValue(state, item.slot));

      // ====== STAGE2 ======
      // 
      const index = Math.floor(Math.random() * randomRange);
      if (Math.random() >= 0.5) {
        //  50%
        item.slot -= Math.floor(stage2List[item.data.avoidId].rateDownList[index] * item.slot);
      }
      if (Math.random() >= 0.5) {
        //  50%
        item.slot -= stage2List[item.data.avoidId].fixDownList[index];
      }

      // 
      Item.updateAirPower(item);
      sumAirPower += item.airPower;
    }

    // 
    landbase.airPower = Math.floor(sumAirPower * landbase.reconCorr);
  }

  /**
   * 
   * Item: airPower, slot
   * @private
   * @static
   * @param {Item[]} items
   * @param {Stage2Table[]} stage2List
   * @return {number} 
   * @memberof Calculator
   */
  private static shootDownJetPhase(items: Item[], stage2List: Stage2Table[]): number {
    let sumAirPower = 0;
    const randomRange = stage2List[0].fixDownList.length;
    for (let j = 0; j < items.length; j += 1) {
      const item = items[j];
      if (!item.isJet) {
        sumAirPower += item.airPower;
        continue;
      }
      // ====== STAGE1 ======
      // 0.6  
      item.slot -= Math.floor(0.6 * CommonCalc.getStage1ShootDownValue(AIR_STATE.KAKUHO, item.slot));

      // ====== STAGE2 ======
      // 
      const index = Math.floor(Math.random() * randomRange);
      if (Math.random() >= 0.5) {
        //  50%
        item.slot -= Math.floor(stage2List[item.data.avoidId].rateDownList[index] * item.slot);
      }
      if (Math.random() >= 0.5) {
        //  50%
        item.slot -= stage2List[item.data.avoidId].fixDownList[index];
      }

      // 
      Item.updateAirPower(item);
      sumAirPower += item.airPower;
    }

    // Item[] 
    return sumAirPower;
  }

  /**
   *   
   * Fleet: airPower
   * Item: airPower, slot
   * @private
   * @static
   * @param {number} state
   * @param {Fleet} fleet
   * @param {Stage2Table[]} stage2List
   * @param {*} [battle=-1] ()  
   * @param {boolean} [disabledStage2=false] Stage2 false
   * @memberof Calculator
   */
  private static shootDownFleet(state: number, fleet: Fleet, stage2List: Stage2Table[], battle = -1, disabledStage2 = false) {
    const items = fleet.allPlanes;
    const randomRange = stage2List ? stage2List[0].fixDownList.length : 0;
    let sumAirPower = 0;
    for (let j = 0; j < items.length; j += 1) {
      const item = items[j];
      if (battle >= 0) {
        // 
        item.slotHistories[battle] += item.slot;
      }

      if (item.isRecon) {
        // 
        continue;
      }

      // ====== STAGE1 ======
      item.slot -= Math.floor(CommonCalc.getStage1ShootDownValue(state, item.slot));

      // ====== STAGE2 ======
      if (!disabledStage2 && stage2List.length && item.isAttacker) {
        // 
        const index = Math.floor(Math.random() * randomRange);
        if (Math.random() >= 0.5) {
          //  50%
          item.slot -= Math.floor(stage2List[item.data.avoidId].rateDownList[index] * item.slot);
        }
        if (Math.random() >= 0.5) {
          //  50%
          item.slot -= stage2List[item.data.avoidId].fixDownList[index];
        }
      }

      // 
      Item.updateAirPower(item);
      sumAirPower += item.airPower;
    }

    fleet.airPower = sumAirPower;
  }

  /**
   *   
   * EnemyFleet: airPower, landbaseAirPower
   * Item: airPower, slot
   * @private
   * @static
   * @param {number} state
   * @param {EnemyFleet} fleet
   * @memberof Calculator
   */
  private static shootDownEnemy(state: number, fleet: EnemyFleet, stage2List?: Stage2Table[]) {
    const items = fleet.allPlanes;
    const randomRange = stage2List ? stage2List[0].fixDownList.length : 0;
    let sumAirPower = 0;
    let sumLandbaseAirPower = 0;
    for (let i = 0; i < items.length; i += 1) {
      const item = items[i];
      // ====== STAGE1 ======
      item.slot -= CommonCalc.getStage1ShootDownValueEnemy(state, item.slot);

      // ====== STAGE2 ======
      if (stage2List && item.isAttacker) {
        // 
        const index = Math.floor(Math.random() * randomRange);
        if (Math.random() >= 0.5) {
          //  50%
          item.slot -= Math.floor(stage2List[item.data.avoidId].rateDownList[index] * item.slot);
        }
        if (Math.random() >= 0.5) {
          //  50%
          item.slot -= stage2List[item.data.avoidId].fixDownList[index];
        }
      }

      // 
      Item.updateAirPower(item);
      if (!item.isRecon) {
        sumAirPower += item.airPower;
      }
      sumLandbaseAirPower += item.airPower;
    }

    fleet.airPower = sumAirPower;
    fleet.landbaseAirPower = sumLandbaseAirPower;
  }
}
